.case{
    height: 500px;
    width: 300px;
    border-radius: 40px;
    background-color: rgb(210,210,210);
    display:flex;
    flex-direction: column;
    align-items: center;
    box-shadow: inset 0 0 20px 3px black;
    
    -webkit-box-reflect: below 0px linear-gradient(to bottom, rgba(0,0,0,0.0), rgba(0,0,0,0.4)); 
}