.wheel-container{
    position: relative;
    width:200px;
    height :200px;
    margin-top:auto;
    display:flex;
    margin-bottom:20px;
}


.wheel{
    border-radius: 50%;
    cursor:pointer;
    position:relative;
    width:200px;
    height:200px;
    background-color: white;
}

.control{
    color:rgb(210,210,210);
    font-weight :900;
   position:absolute;
}

#menu{
    left:38%;
    top:11%;
    font-weight:900;
    width:40px;
    height: 20px;
}

#reverse{
     left:10%;
      top:45%;
      font-size :23px;
}

#forward{
    right:10%;
    top:45%;
    font-size:23px;
}

#play-pause{
    bottom:11%;
    left:44%;
    width:40px;
    height:20px;
}

.blank{
    width:80px;
    height:80px;
    border-radius: 50%;
    background-color : rgb(210,210,210);
    position:absolute;
    left:31%;
    top:30%;

}