.now-playing-container{
    width:270px;
    height:190px;
    background-color: #e3f2fd;

}

.song-details{
    display:flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    padding:10px;
}

.song-details img{
    margin:auto;
    margin-top:10px;
    width:110px;
    height:110px;
}

.song-details h6{
    margin:2px;
    margin-left: 5px;
    font-size:10px;
}

.status{
    margin-top:20px;
    display:flex;
    align-items: center;
    justify-content: center;
    width:100%;
}

#progress{
    width:65%;
    height:10px;
    margin:0px 5px;
    background-color: grey;
}

#progress-bar{
    height:10px;
    background-color: green;
}

.play-pause-nav{
    margin-top:20px;
    font-size: 18px;
}